<template>
  <div class="history">
    <ul>
      <li v-for="(item,index) in historyList" :key="index" class="history__item">
        <a @click="handleClick(item)" class="history__img-wrapper default-bg-small" href="javascript:void (0);">
          <img v-lazy="$useWebpImg(item.imgUrl)" class="history__img" alt="">
          <i @click.stop="handleClick(item,false)" v-if="item.video||item.imgList" class="history__icon-video"></i>
        </a>
        <p class="history__title">
          {{item.title}}
        </p>
        <p class="history__date">
          {{item.date}}
        </p>
        <p class="history__sub-title">
          {{item.subtitle}}
        </p>
        <span class="history__dot"></span>
      </li>
    </ul>
    <ImgDialog :showImgDialog.sync="showImgDialog" :imgList="imgList"></ImgDialog>
  </div>
</template>

<script>
import ImgDialog from '@/components/dialog/ImgDialog.vue'

export default {
  name: 'history',
  components: {
    ImgDialog
  },
  data() {
    return {
      imgList: [],
      historyList: [
        {
          title: '2019凯发年度传记',
          subtitle: '2019凯发年度报告，感谢会员一路相随',
          date: '2020年2月',
          imgUrl: '@/assets/img/mien/history/history_1.jpg'
        },
        {
          title: '凯发娱乐正式收购暴风竞技',
          subtitle: '博彩行业全方位多元化布局',
          date: '2020年2月',
          imgUrl: '@/assets/img/mien/history/history_2.jpg'
        },
        {
          title: '凯发娱乐第二届「凯发赌王」诞生',
          subtitle: '凯发带您一睹赌王绰约风姿',
          date: '2019年10月',
          imgUrl: '@/assets/img/mien/history/history_3.jpg',
          imgList: [
            {
              video: 'baccc.mp4',
              imgUrl: '@/assets/img/mien/history/kf_bet_king/1.jpg'
            },
            {
              imgUrl: '@/assets/img/mien/history/kf_bet_king/2.jpg'
            },
            {
              imgUrl: '@/assets/img/mien/history/kf_bet_king/3.jpg'
            },
            {
              imgUrl: '@/assets/img/mien/history/kf_bet_king/4.jpg'
            },
            {
              imgUrl: '@/assets/img/mien/history/kf_bet_king/5.jpg'
            },
            {
              imgUrl: '@/assets/img/mien/history/kf_bet_king/6.jpg'
            }
          ]
        },
        {
          title: '凯发娱乐正式更换Logo',
          subtitle: '全新升级上线',
          date: '2019年10月',
          imgUrl: '@/assets/img/mien/history/history_4.jpg'
        }
      ],
      showImgDialog: false,
      showVideoDialog: false
    }
  },
  methods: {
    openImgDialog(item) {
      this.imgList = item.imgList
      this.showImgDialog = true
    },
    openVideoDialog(item) {
      this.video = item.video
      this.poster = this.$useWebpImg(item.imgUrl)
      this.showVideoDialog = true
    },
    handleClick(item, isOpenLink = true) {
      if (item.link && isOpenLink) {
        this.$router.push(item.link)
      } else if (item.imgList) {
        this.openImgDialog(item)
      } else if (item.video) {
        this.openVideoDialog(item)
      }
    }
  }
}
</script>

